.avatarWrapper img {
  width: 100%;
    height: 100%;
    transition: all 1s;
}

.avatarWrapper img:hover {
  transform: scale(1.1);
}


.avatarWrapper {
  overflow: hidden;
    border: 1px solid;
    width: 250px;
    height: 250px;
    margin: 12px 0px;
    border-radius: 200px;
}